.container {
  display: flex;
  max-width: 1000px;
  margin: 0 auto;
  margin-top: 70px;
  align-items: end;
  position: relative;
  top: -100px;
  padding-bottom: 0px;
}
.top_btn {
  display: flex;
  gap: 32px;
}
.contact_form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.contact_form form {
  display: flex;
  gap: 20px;
  flex-direction: column;
  padding-bottom: 10px;
}
.form_control {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}
.form_control input {
  height: 40px;
  padding: 0 8px;
}
.form_control textarea {
  padding: 8px;
}
.form_control label {
  font-size: 14px;
  position: absolute;
  top: -12px;
  left: 10px;
  background-color: white;
  padding: 0 10px;
}
.contact_image {
  position: relative;
  top: -25px;
}
